import React, { Suspense, useContext } from "react";
import { Route, Routes, Navigate } from "react-router-dom";

import Contex from "./Components/Store/Contex";
import "./Routing.css";
const Login = React.lazy(() => import("./Components/LoginPage/Login"));
const Store = React.lazy(() => import("./Components/Storepage/Store"));
const Item = React.lazy(() => import("./Components/ItemDetails/Item"));
const About = React.lazy(() => import("./Components/About/About"));

const Home = React.lazy(() => import("./Components/HomePage/Home"));
const ContactUsPage = React.lazy(() =>
  import("./Components/ContactUs/ContactUsPage")
);

const Routing = () => {
  const contexVal = useContext(Contex);
  return (
    <Suspense
      fallback={
        <div className="spinner">
          <div className="loader"></div>
        </div>
      }
    >
      <Routes>
        <Route path="/login" element={<Login />} />

        <Route
          path="/store"
          element={
            contexVal.isLogin ? (
              <Store />
            ) : (
              <Navigate to="/login" replace={true} />
            )
          }
        />
        <Route path="/store/:items" element={<Item />} />
        <Route path="/about" element={<About />} />
        <Route path="/" element={<Home />} />
        <Route path="/contactus" element={<ContactUsPage />} />
      </Routes>
    </Suspense>
  );
};

export default Routing;
